.home {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  background-position: center;
  background-size: cover;
}

.headerContainer {
  width: auto;
  margin-left: 18px;
  margin-top: -390px;
}
.home .headerContainer h1 {
  justify-content: center;
  align-items: center;
  font-size: 70px;
  font-family: "Roboto", sans-serif;
  color: #fff;
  text-transform: uppercase;
}
.home .headerContainer p {
  margin-top: 15px;
  padding: 0;
  font-size: 40px;
  font-weight: lighter;
  color: #fff;
}
.home .headerContainer button {
  background-color: #b7a477;
  margin-top: 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  height: 50px;
  width: 180px;
  text-decoration: none;
  color: white;
  font-size: 20px;
}
.headerContainer button:hover {
  background-color: #122f83;
  cursor: pointer;
  transition: 0.3s ease-in;
}
@media only screen and (max-width: 600px) {
  .home {
    justify-content: center;
    align-items: center;
  }
  .headerContainer {
    margin-left: 0;
    border-radius: 10px;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #122f83;
    color: white;
  }
  .home .headerContainer h1 {
    font-size: 40px;
    height: 30px;
    margin: 30px 0px;
    color: white;
  }
  .home .headerContainer p {
    font-size: 30px;
    color: white;
  }
  .headerContainer button {
    background-color: white;
    color: #122f83;
    margin-bottom: 30px;
  }
}
